.play {
  color: white;
  
  &__top {
    display: flex;
    justify-content: space-between;
  }

  &__score,
  &__level {
    display: inline;
    margin-top: 10px;
  }

  &__link {
    padding: 10px;
    margin: 10px;
    display: block;
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    color: white;
    border-radius: 10px;
    background-color: rgba(0,0,0,0.7);

    &:hover {
      cursor: pointer;
      box-shadow: 0 0 5px 0 white;
    }
  
    &:active {
      box-shadow: inset 0 0 5px 0 white;
    }
  }
}

